.board-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.board-wrapper .board {
    display: flex;
    flex-wrap: wrap;
    width: 640px;
    height: 640px;
}
.board-wrapper .board .field {
    width: calc(640px / 3);
    height: calc(640px / 3);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13rem;
}
.board-wrapper .board .field:not(:nth-child(3n)) {
    border-right: 0.75rem solid black;
}
.board-wrapper .board .field:not(:nth-child(n+7)) {
    border-bottom: 0.75rem solid black;
}
.board-wrapper .board .field:nth-child(3n+1):hover {
    background: linear-gradient(to right, #fff 40%, #1900ff 40% 100%);
}
.board-wrapper .board .field:nth-child(3n+2):hover {
    background: linear-gradient(to right, #fff 40%, red 40% 100%);
}
.board-wrapper .board .field:nth-child(3n+3):hover {
    background: linear-gradient(to right, #fff 40%, green 40% 100%);
}
.board-wrapper .board .field .x-mark {
    height: 60px;
    width: 60px;
}

